
import { clientInquiries, type ClientInquiry } from "@/lib/placeholder-data";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { Eye, Edit } from "lucide-react";
import AIQuoteGeneratorClient from "@/components/admin/AIQuoteGeneratorClient";
import { format } from 'date-fns';

// For this prototype, we'll manage "selected inquiry" via query param or simple state.
// A real app would use a more robust state management or separate pages for detail view.

export default function AdminInquiriesPage({ searchParams }: { searchParams?: { id?: string } }) {
  const selectedInquiryId = searchParams?.id;
  const selectedInquiry = selectedInquiryId 
    ? clientInquiries.find(inq => inq.id === selectedInquiryId) 
    : null;

  // Mock function to update inquiry status or generated quote
  // In a real app, this would be a server action updating a database.
  const handleQuoteGenerated = (inquiryId: string, quote: any) => {
    console.log("Quote generated for inquiry:", inquiryId, quote);
    // Here you would update your data store with the generated quote.
    // For the prototype, this is just a log.
    // You might want to re-fetch data or use a client-side state update if not using server components for the list.
  };

  return (
    <div className="space-y-8">
      <h1 className="text-3xl font-bold text-primary">Client Inquiries</h1>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        <div className="lg:col-span-2">
          <Card className="shadow-lg">
            <CardHeader>
              <CardTitle className="text-xl">All Inquiries</CardTitle>
              <CardDescription>View and manage all client quote requests.</CardDescription>
            </CardHeader>
            <CardContent>
              <Table>
                <TableHeader>
                  <TableRow>
                    <TableHead>Client</TableHead>
                    <TableHead>Service</TableHead>
                    <TableHead className="hidden md:table-cell">Date</TableHead>
                    <TableHead>Status</TableHead>
                    <TableHead className="text-right">Actions</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {clientInquiries.map((inquiry) => (
                    <TableRow key={inquiry.id} className={selectedInquiryId === inquiry.id ? "bg-muted/50" : ""}>
                      <TableCell>
                        <div className="font-medium">{inquiry.name}</div>
                        <div className="text-xs text-muted-foreground hidden sm:block">{inquiry.email}</div>
                      </TableCell>
                      <TableCell>{inquiry.serviceRequested}</TableCell>
                      <TableCell className="hidden md:table-cell">{format(new Date(inquiry.date), "MMM d, yyyy")}</TableCell>
                      <TableCell>
                        <Badge variant={
                          inquiry.status === 'New' ? 'default' :
                          inquiry.status === 'Contacted' ? 'secondary' :
                          inquiry.status === 'Quoted' ? 'outline' : // Use accent for quoted if desired
                          'destructive' // For 'Closed' or other final states
                        }
                        className={inquiry.status === 'Quoted' ? 'border-accent text-accent' : ''}
                        >
                          {inquiry.status}
                        </Badge>
                      </TableCell>
                      <TableCell className="text-right">
                        <Button variant="ghost" size="icon" asChild>
                          <Link href={`/admin/inquiries?id=${inquiry.id}`} title="View Details">
                            <Eye className="h-4 w-4" />
                          </Link>
                        </Button>
                        {/* Add Edit button if needed */}
                        {/* <Button variant="ghost" size="icon" asChild><Link href={`/admin/inquiries/edit/${inquiry.id}`} title="Edit"><Edit className="h-4 w-4" /></Link></Button> */}
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
              {clientInquiries.length === 0 && (
                <p className="p-4 text-center text-muted-foreground">No inquiries found.</p>
              )}
            </CardContent>
          </Card>
        </div>

        <div className="lg:col-span-1 space-y-6">
          {selectedInquiry ? (
            <Card className="shadow-lg sticky top-6">
              <CardHeader>
                <CardTitle className="text-xl text-primary">Inquiry Details</CardTitle>
                <CardDescription>From: {selectedInquiry.name} ({selectedInquiry.email})</CardDescription>
              </CardHeader>
              <CardContent className="space-y-3 text-sm">
                <p><strong>Service Requested:</strong> {selectedInquiry.serviceRequested}</p>
                <p><strong>Date:</strong> {format(new Date(selectedInquiry.date), "PPP")}</p>
                <p><strong>Status:</strong> <Badge variant={
                    selectedInquiry.status === 'New' ? 'default' :
                    selectedInquiry.status === 'Contacted' ? 'secondary' :
                    selectedInquiry.status === 'Quoted' ? 'outline' : 
                    'destructive'
                  } className={selectedInquiry.status === 'Quoted' ? 'border-accent text-accent' : ''}>{selectedInquiry.status}</Badge>
                </p>
                <div>
                  <h4 className="font-semibold">Request Details:</h4>
                  <p className="whitespace-pre-wrap p-2 border rounded-md bg-muted/30 text-xs max-h-40 overflow-y-auto">{selectedInquiry.details}</p>
                </div>
              </CardContent>
              <CardFooter>
                {/* Actions like "Mark as Contacted", "Change Status" could go here */}
              </CardFooter>
            </Card>
          ) : (
             <Card className="shadow-lg sticky top-6">
                <CardContent className="p-6 text-center text-muted-foreground">
                    Select an inquiry from the list to view details and generate a quote.
                </CardContent>
            </Card>
          )}
          
          {selectedInquiry && (
             <AIQuoteGeneratorClient 
                key={selectedInquiry.id} // Ensure re-render if selectedInquiry changes
                initialRequestDetails={selectedInquiry.details}
                onQuoteGenerated={(quote) => handleQuoteGenerated(selectedInquiry.id, quote)}
            />
          )}

          {selectedInquiry?.generatedQuote && (
            <Card className="shadow-md bg-green-50 border-green-200">
              <CardHeader>
                <CardTitle className="text-lg text-green-700">Previously Generated Quote</CardTitle>
              </CardHeader>
              <CardContent className="space-y-2 text-xs">
                <div>
                  <h4 className="font-semibold text-green-600">Project Scope:</h4>
                  <p className="whitespace-pre-wrap text-green-800/80 p-1 border border-green-200 rounded-md bg-white">{selectedInquiry.generatedQuote.projectScope}</p>
                </div>
                <div>
                  <h4 className="font-semibold text-green-600">Cost Estimate:</h4>
                  <p className="whitespace-pre-wrap text-green-800/80 p-1 border border-green-200 rounded-md bg-white">{selectedInquiry.generatedQuote.costEstimate}</p>
                </div>
                <div>
                  <h4 className="font-semibold text-green-600">Additional Notes:</h4>
                  <p className="whitespace-pre-wrap text-green-800/80 p-1 border border-green-200 rounded-md bg-white">{selectedInquiry.generatedQuote.additionalNotes}</p>
                </div>
              </CardContent>
            </Card>
          )}

        </div>
      </div>
    </div>
  );
}
